import React, { useCallback, useContext, useEffect, useState, useMemo, useDebugValue} from "react"

import classnames from "classnames"

import { Collapse } from 'reactstrap'

import {ScenarioContext} from "../../contexts/ScenarioContext"

import CheckBoxIcon from "../icons/CheckBoxIcon"
import ChevronIcon from "../icons/ChevronIcon"
import DoneIcon from "../icons/DoneIcon"


export const NestedForm = ({id, label, checkedWhen = "some", children}) => {
    const { buildDepListFromCheckListItemIDs, checkListItems, isChecked, setDerivedCheckListItemValue, shouldDisplayInterventionForm, toggleDisplayInterventionForm} = useContext(ScenarioContext)
    const depIDs = useMemo(() => children.map(child => child.id), [...children])
    const depList = useCallback(() => buildDepListFromCheckListItemIDs(depIDs), [checkListItems])

    useEffect(
        () => {setDerivedCheckListItemValue(id, checkedWhen, depIDs)},
        [depList]
    )

    return (
        <li>
            <span onClick={() => toggleDisplayInterventionForm(id)}>
                <span className={classnames("material-icons-outlined", { "invisible": !isChecked(id) })}>
                    check
                </span>
                {label}
                <ChevronIcon expand={shouldDisplayInterventionForm(id)} />
             </span>

            <Collapse isOpen={shouldDisplayInterventionForm(id)} >
                <ul>
                {children.map(child =>
                    <li key={child.id}><IntvFormItem item={child} /></li>
                )}
                </ul>
            </Collapse>

        </li>
    )
}


export const DerivedValue = ({id, checkedWhen, children}) => {

    const {buildDepListFromCheckListItemIDs, checkListItems, setDerivedCheckListItemValue } = useContext(ScenarioContext)
    const depIDs = useMemo(() => children.map(child => child.id), [...children])
    const depList = useCallback(() => buildDepListFromCheckListItemIDs(depIDs), [checkListItems])
    // set the derived value on the initial render
    useEffect(
        () => { setDerivedCheckListItemValue(id, checkedWhen, depIDs) },
        []
    )

    // set the derived value when something changes
    useEffect(
        () => {setDerivedCheckListItemValue(id, checkedWhen, depIDs)},
        [depList]
    )

    return (
        <>
            {children.map(child =>
                <IntvFormItem item={child} />
            )}
        </>
    )
}


export const FormCheckBox = ({ id, label, clickHandler }) => {
    const { isChecked } = useContext(ScenarioContext)

    return (
        <span onClick={() => clickHandler(id)}>
            <CheckBoxIcon checked={isChecked(id)} />
            <span>
                {label}
            </span>
        </span>
    )
}

export const DropDownMenu = ({ id, label, children }) => {

    const { buildDepListFromCheckListItemIDs, checkListItems, isChecked, setChecked, setDerivedCheckListItemValue } = useContext(ScenarioContext)
    const depIDs = useMemo(() => children.map(child => child.id), [...children])
    const depList = useCallback(() => buildDepListFromCheckListItemIDs(depIDs), [checkListItems])

    useEffect(
        () => { setDerivedCheckListItemValue(id, "some", depIDs) },
        []
    )

    // set the derived value when something changes
    useEffect(
        () => { setDerivedCheckListItemValue(id, "some", depIDs) },
        [depList]
    )

    const changeHandler = (event) => {
        // FIXME: we need to setChecked(false) when an item is de-selected. perhaps useState()?
        const selected = event.target.value
        const notSelected = depIDs.filter(dep => dep !== selected)
        console.log("selected", selected)
        console.log("not selected", notSelected)
        notSelected.forEach(id => setChecked(id, false))
        if ("" !== selected) {
            setChecked(selected, true)
        }
    }

    // console.log(children)
    return (
        <li>
            {/* <fieldset> */}
                {/* <legend> */}
                    <span className={classnames("material-icons-outlined", { "invisible": !isChecked(id) })}>
                        check
                    </span>
                    <i>{label}</i>
                {/* </legend> */}
            <ul>
                <li>
                    <select
                        id={id}
                        onChange={changeHandler}
                        className="form-control"
                    >
                        <option value="">----</option>
                        {children.map(child => (
                            <option key={child.id} value={child.id} selected={isChecked(id)}>{child.label}</option>
                        ))}
                    </select>
                </li>
            </ul>
            {/* </fieldset> */}
        </li>
    )
}

export const SelectOneOrMore = ({id, label, checkedWhen, children}) => {

    const { buildDepListFromCheckListItemIDs, checkListItems, isChecked, setDerivedCheckListItemValue} = useContext(ScenarioContext)

    const depIDs = useMemo(() => children.map(child => child.id), [...children])
    const depList = useMemo(() => buildDepListFromCheckListItemIDs(depIDs), [checkListItems])

    // set the derived value on initial render
    useEffect(
        () => { setDerivedCheckListItemValue(id, checkedWhen, depIDs) },
        []
    )

    // set the derived value when something changes
    useEffect(
        () => {setDerivedCheckListItemValue(id, checkedWhen, depIDs)},
        [...depList]
    )

    return (
        <li>
            {/* <fieldset> */}
                {/* <legend> */}
                <span className={classnames("material-icons-outlined", { "invisible": !isChecked(id) })}>
                    check
                </span>
                <i>{label}</i>
                {/* </legend> */}
                <ul>
                    {children.map((child) =>
                        <li key={child.id}><IntvFormItem item={child} /></li>
                    )}
                </ul>
            {/* </fieldset> */}
        </li>
    )
}


export const IntvFormItem = ({ item }) => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    switch (item.type) {
        case "derived-value":
            return (
                <DerivedValue
                    id={item.id}
                    checkedWhen={item.checkedWhen}
                    children={item.children}
                />
            )
        case "select-one-or-more":
            return (
                <SelectOneOrMore
                    id={item.id}
                    label={item.prompt}
                    checkedWhen={item.checkedWhen}
                    children={item.children}
                />
            )
        case "drop-down-menu":
            return (
                <DropDownMenu
                    id={item.id}
                    label={item.prompt}
                    children={item.children}
                />
            )
        case "checkbox":
            return (
                <FormCheckBox
                    id={item.id}
                    label={item.label}
                    clickHandler={toggleChecked}
                />
            )
        case "prompt":
            return (<li><i>{item.label}</i></li>)
        case "nested-form":
            return (
                <li>
                    <NestedForm
                        id={item.id}
                        label={item.label}
                        checkedWhen={item.checkedWhen || "some"}
                        children={item.children}
                    />
                </li>
            )
        default:
            return (<p>placeholder for unhandled item type</p>)
    }
}

export const IntvForm = ({ data }) => {
    const {id, label, checkedWhen, children} = data
    const {buildDepListFromCheckListItemIDs, checkListItems, isChecked, setDerivedCheckListItemValue} = useContext(ScenarioContext)

    const depIDs = useMemo(() => children.map(child => child.id), [...children])
    const depList = useMemo(() => buildDepListFromCheckListItemIDs(depIDs), [checkListItems])

    useEffect(
        () => { setDerivedCheckListItemValue(id, checkedWhen, depIDs)},
        []
    )

    useEffect(
        () => { setDerivedCheckListItemValue(id, checkedWhen, depIDs) },
        [depList]
    )

    return (
        <>
        <span className="h5">
            <DoneIcon checked={isChecked(id)} />
            {label}
        </span>

        <hr className="minimal"/>

        <ul>
            {children.map((rec) =>
                <IntvFormItem key={rec.id} item={rec} />
            )}
        </ul>
        </>
    )
}
