import React from "react"

import { IntvForm } from "./FormUtils"

const ManageSecondaryInjuries = () => {

    const FORM_DATA = {
        id: "intv-manage-secondary-injuries",
        type: "form",
        label: "Secondary Wounds",
        checkedWhen: "all",
        children: [
            {
                id: "intv-manage-secondary-injuries-techniques",
                type: "select-one-or-more",
                prompt: "What technique(s) will you use to manage secondary wounds/injuries?",
                checkedWhen: "some",
                children: [
                    { id: "intv-manage-secondary-injuries-apply-dressings", label: "Apply dressings Foo", type: "checkbox" },
                    { id: "intv-manage-secondary-injuries-split-fractures", label: "Splint fractures", type: "checkbox" },
                    { id: "intv-manage-secondary-injuries-other", label: "Other", type: "checkbox" }
                ]
            }

        ]
    }

    return (
        <IntvForm data={FORM_DATA} />
    )

}

export default ManageSecondaryInjuries